.service-plans-section {
  position: relative;
  z-index: 1;
  background: #f6f6f6;
  padding-top: 60px;
  
  @include media($min: $screen-lg) {
    padding-top: 162px;
    padding-bottom: 60px;
  }
}

.service-plans-sup-title {
  max-width: 400px;
  margin-bottom: 12px;
}

.service-plans-title {
  @include media($min: $screen-lg) {
    margin-bottom: 47px;
  }
}

.service-plans-list {
  display: flex;
  
  @include media($max: $screen-lg) {
    flex-direction: column;
  }
  
  @include media($min: $screen-lg) {
    justify-content: space-between;
  }
}

.service-plans-item {
  display: flex;
  flex-direction: column;
  
  @include media($max: $screen-lg) {
    position: relative;
    padding-top: 33px;
    padding-bottom: 23px;

    &:nth-of-type(2) {
      background: $white;
      padding-left: 30px;
      padding-right: 30px;
      margin-left: -30px;
      margin-right: -30px;
    }
  }

  @include media($max: $screen-md) {
    &:nth-of-type(2) {
      padding-left: 20px;
      padding-right: 20px;
      margin-left: -20px;
      margin-right: -20px;
    }
  }
  
  @include media($min: $screen-lg) {
    width: 33.33333%;
    max-width: 240px;
  }
}

.service-plans-item-heading {
  position: relative;
  margin-bottom: 22px;
  
  @include media($max: $screen-lg) {
    max-width: 240px;
    order: -1;
    margin-bottom: 8px;
  }
}

.service-plans-item-cc {
  position: absolute;
  bottom: calc(100% - 23px);
  left: 0;
  padding-left: 25px;
  font-size: rem-calc(14px);
  line-height: ren-calc(20px);
  color: $accent-color;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: $accent-color; 
  }

  &:after {
    content: '';
    position: absolute;
    top: 7px;
    left: 6px;
    width: 8px;
    height: 4px;
    border-bottom: 1px solid $white;
    border-left: 1px solid $white;
    transform: rotate(-45deg);
  }
}

.service-plans-item-title {
  padding-top: 30px;
  padding-bottom: 5px;

  @include media($min: $screen-lg) {
    padding-bottom: 20px;
  }
}

.service-plans-item-sub-title {
  font-size: rem-calc(14px);
  line-height: ren-calc(20px);
  color: rgba(42, 40, 41, 0.5);

  @include media($min: $screen-lg) {
    position: absolute;
    top: calc(100% - 17px);
    left: 0;
  }
}

.service-plans-item-options {
  margin-bottom: 40px;

  @include media($max: $screen-lg) {
    margin-top: 10px;
    display: none;
  }
}

.service-plans-item-option {
  position: relative;
  font-size: rem-calc(14px);
  line-height: ren-calc(20px); 
  color: $black;

  & + & {
    margin-top: 10px;
  }

  a {
    color: $black;
    text-decoration: underline;
  }  

  &:before {
    content: "+"; 
    position: absolute; 
    top: 0;
    left: -40px;
    width: 20px;
    height: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    border: 1px solid $black;
  }

  @include media($max: $screen-lg) {
    padding-left: 40px;

    &:before {
      left: 0;
    }
  }
}

.service-plans-item-info {
  a {
    color: $black;
    text-decoration: underline;
  }

  @include media($max: $screen-lg) {
    max-width: 240px;
    order: -1;
    font-size: rem-calc(14px);
    line-height: rem-calc(20px);
    margin-bottom: 10px;
  }

  @include media($min: $screen-lg) {
    position: relative;
    padding-top: 15px;
    padding-bottom: 40px;

    &:before,
    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: $black;
    }

    &:after {
      top: auto;
      bottom: 0; 
    }

    .service-plans-item:nth-of-type(2) & {
      &:before,
      &:after {
        left: 50%;
        transform: translateX(-50%);
        width: calc(100% + 140px);
      }
    }
  }
}

.service-plans-item-bottom {
  flex: 1;
  padding-bottom: 20px;
  
  @include media($max: $screen-lg) {
    display: flex;
    justify-content: space-between;
  }  
  
  @include media($min: $screen-lg) {
    position: sticky;
    bottom: 0;
    padding-top: 20px;
    z-index: 1;

    .service-plans-item:first-of-type &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 200vw;
      height: 100%;
      transform: translateX(-50%);
      background: #f6f6f6;
    }
  
    .button {
      position: relative;
      z-index: 1;
    }
  }
}

.service-plans-item-price {
  position: relative;
  margin-bottom: 10px;
  z-index: 1;

  @include media($max: $screen-lg) {
    position: absolute;
    top: 60px;
    right: 0;  

    span {
      display: block;
      font-size: rem-calc(14px);
      line-height: rem-calc(20px);
    }

    .service-plans-item:nth-of-type(2) & {
      right: 20px;  
    }
  }
}

.service-plans-item-toggle-plan {
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid rgba(42, 40, 41, 0.04);
  transition: transform 0.3s ease;

  &:before,
  &:after {
    content: '';
    position: absolute;
    top: 12px;
    left: 18px;  
    width: 14px;
    height: 14px;
    border-bottom: 1px solid rgba(42, 40, 41, 0.5);
    border-left: 1px solid rgba(42, 40, 41, 0.5);
    transform: rotate(-45deg);
  }

  &:after {
    top: 18px;
  }

  &.opened {
    transform: rotate(180deg);
  }

  @include media($min: $screen-lg) {
    display: none;
  }
}
