.main-section {
  padding-top: 90px !important;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  overflow-y: auto;
  z-index: 99;
  
  @include media($max: $screen-md) {
    background-image: none !important;
    padding-bottom: 20px;
  }
}

.main-section__inner {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  min-height: 100%;
}

.main-section__top {
  margin-bottom: 17px;
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 0.5s ease, transform 0.5s ease;

  
  @include media($min: $screen-md) {
    flex: 1;
    margin-bottom: 30px;
  }
}

.main-section__heading {
  padding-top: 0;
}

.main-section__sub-heading {
  margin-top: 10px;
  
  svg {
    width: 97px;
    height: auto;
  }
  
  @include media($min: $screen-md) {
    margin-top: 20px;

    svg {
      width: 127px;
    }
  }
}

.main-section__center,
.main-section__bottom {
  opacity: 0;
  transition: opacity 0.5s ease;
}

.main-section__button:first-of-type {
  margin-right: 20px;
  
  @include media($max: $screen-md) {
    margin-bottom: 15px;
  }
}

.main-section__clutch {
  position: absolute;
  bottom: 0;
  right: 0;
  opacity: 0;
  transition: opacity 0.5s ease;

  @include media($max: $screen-md) {
    svg {
      width: 16px;
      height: auto;
    }
  }
}

.main-section__center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex: 1;
  position: relative;
  
  @include media($max: $screen-md) {
    margin-bottom: 60px;
  }

  @include media($min: $screen-md) {
    position: absolute;
    top: 44%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 35%;
    height: 70%;
  }
}

.main-section__center-inner {
  @include media($min: $screen-md) {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}

.main-section__action,
.main-section__action:before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.main-section__center-inner .svg-wrap {
  display: flex;
}

.main-section__center-inner .svg-wrap:nth-of-type(2) {
  position: absolute;
  top: calc(50% - 54px);
  left: calc(50% - 54px);
}

.main-section__action {
  width: 40px;
  height: 40px;
  border: 1px solid rgba(255, 255, 255, 0.33);
  border-radius: 50%;
  transition: width 0.4s ease, height 0.4s ease;

  &:before {
    content: '';
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background: $accent-color;
    transition: width 0.6s ease, height 0.6s ease, background 0.4s ease;
  }

  &:hover {
    width: 50px;
    height: 50px;
  }
}

.main-section__center-inner .svg-wrap:nth-of-type(1) svg {
  animation: 35s linear 0s none infinite running rot;
}

.main-section__center-inner .svg-wrap:nth-of-type(1) {
  animation: 17s linear 0s reverse none infinite running rot;
  animation-play-state: paused;
}

.main-section__center-inner .svg-wrap:nth-of-type(2) svg {
  animation: 35s linear 0s reverse none infinite running rot;
}

.main-section__center-inner .svg-wrap:nth-of-type(2) {
  animation: 17s linear 0s none infinite running rot;
  animation-play-state: paused;
}

.main-section__center-inner.running-reverse .svg-wrap:nth-of-type(1),
.main-section__center-inner.running-reverse .svg-wrap:nth-of-type(2) {
  animation-play-state: running;
}

@keyframes rot {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.main-section.closing {
  overflow: hidden;

  .main-section__action:before {
    width: 200vw;
    height: 200vw;
    background: #fff;
  }
}

body.on-load {
  .main-section__top {
    opacity: 1;
    transform: translateX(0);
  }

  .main-section__clutch,
  .main-section__center,
  .main-section__bottom {
    opacity: 1;
  }
}

.main-section__bg-video {
  position: absolute;
	min-width: 100%;
	min-height: 100%;
	width: auto;
	height: auto;
	top: 50%;
	left: 50%;
  object-fit: contain;
  transform: translate(-50%,-50%);
  opacity: 0.15;
  
  @include media($max: $screen-md) {
    display: none;
  }
}
