.contact-form-section {
  position: relative;
  padding-top: 30px;
  padding-bottom: 30px;
  z-index: 1;
  
  @include media($min: $screen-md) {
    padding-bottom: 90px;
  }

  .contact-form-heading {
    max-width: 640px;
    font-family: $heading-font;
    font-size: rem-calc(44px);
    line-height: rem-calc(40px);
    font-weight: 500;
    letter-spacing: -0.02em;
    margin-bottom: 37px;

    @include media($min: $screen-md) {
      font-size: rem-calc(64px);
      line-height: rem-calc(60px);
    }
  }

  .contact-form {
    display: flex;
    flex-wrap: wrap;

    @include media($max: $screen-md) {
      margin: 0 -20px;
    }  
  }

  .form-group {
    width: 100%;

    &.success:before {
      content: "";
      top: 27px;
      right: 38px;
      width: 30px;
      height: 16px;
    }

    &.required:before {
      right: 38px;
      font-size: 18px;
    }

    @include media($max: $screen-md) {
      &.success:before,
      &.required:before {
        right: 18px;
      }
    }

    @include media($min: $screen-md) {
      border-bottom: 1px solid $black;
      
      &:first-of-type {
        border-top: 1px solid $black;
      }

      &.email-field,
      &.phone-field {
        width: 50%;
      }
  
      &.email-field {
        border-right: 1px solid $black;
        padding-right: 20px;
  
        &.required:before {
          right: 20px;
        }
      }
  
      &.phone-field {
        padding-left: 20px;
  
        .form-label {
          left: 20px;
        }
      }
    }
  }

  .form-label {
    top: 25px;
    font-size: rem-calc(19px);
    line-height: rem-calc(30px);
  
    &.focused {
      font-size: rem-calc(14px);
      line-height: rem-calc(20px);
    }

    @include media($max: $screen-md) {
      left: 20px;
    }
  }

  .form-control {
    font-size: rem-calc(19px);
    line-height: rem-calc(30px);
    padding: 32px 85px 17px 0;

    @include media($max: $screen-md) {
      padding-left: 20px;
      padding-right: 65px;
    }
  }

  .form-btn {
    margin-top: 30px;

    @include media($max: $screen-md) {
      margin-left: 20px;
    }
  }
}

.contact-form-section-avatar {
  position: absolute;
  top: 95px;
  right: calc(50% - 650px);
  width: 200px;
  height: 200px;
  border-radius: 50%;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include media($max: 1340px) {
    display: none;
  }
}
