//contact form
.contact-form-wrap {
  display: flex;
  flex-direction: column;
}

.contact-form {
  flex: 1;
}

.form-group {
  position: relative;
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid rgba($black, 0.2);

  &:first-of-type {
    border-top: 1px solid rgba($black, 0.2);
  }

  &.required:before {
    content: '*';
    position: absolute;
    top: 12px;
    right: 0;
    font-size: rem-calc(14px);
  }

  &.success:before {
    content: '';
    position: absolute;
    top: 18px;
    right: 0;
    transform: rotate(-45deg);
    width: 16px;
    height: 10px;
    border-left: 1px solid #000;
    border-bottom: 1px solid $black
  }
}

.form-group.select .form-label {
  top: 10px;
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);
  color: rgba($black, 0.2);
}

.form-group.select:before {
  content: none !important; 
} 

.form-label,
.form-control {
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);
}

.form-label {
  position: absolute;
  top: 20px;
  left: 0;
  color: rgba($black, 0.5);
  transition: color 0.2s ease-in-out, transform 0.2s ease-in-out; 

  &.focused {
    transform: translateY(-10px);
    color: rgba($black, 0.2);
  }
}

.form-control {
  color: $font-color;
  border: none;
  outline: none;
  background: transparent;
  padding: 32px 20px 8px 0;
}

select.form-control {
  height: 80px;
}

.form-btn {
  margin-top: 20px;
}
