.container {
  width: 100%;
  max-width: 920px;
  padding-left: 20px;
  padding-right: 20px;
  margin-left: auto;
  margin-right: auto;

  @media screen and (min-width: $screen-md) {
    padding-left: 30px;
    padding-right: 30px;
  }
}

.container-fluid {
  width: 100%;
  padding-left: 20px;
  padding-right: 20px;

  @media screen and (min-width: $screen-md) {
    padding-left: 30px;
    padding-right: 30px;
  }
}
