.buisness-intro-section {
  overflow: hidden;
  padding: 0;
}

.buisness-intro-section__services-list {
  display: flex;
  margin-top: 15px;
  flex-wrap: wrap;

  @include media($min: $screen-md) {
    flex-wrap: nowrap;
    margin-top: 40px;
  }
}

.buisness-intro-section__services-item {
  position: relative;
  color: #fff;
  padding: 0 20px 0 0;
  margin-right: 20px;
  cursor: pointer;

  &:before {
    content: '';
    position: absolute;
    top: 50%;
    right: -9px;
    width: 20px;
    height: 1px;
    background: #fff;
    opacity: 0.2;
  }

  span {
    cursor: pointer;
  }

  @include media($max: $screen-md) {
    &:last-of-type:before {
      display: none;
    }
  }

  @include media($min: $screen-md) {
    width: 255px;
    padding: 20px 20px 20px 0;
    transition: opacity 0.25s ease;

    &.not-active {
      opacity: 0.2;
    }

    &:before {
      top: 0;
      left: 0;
      right: auto;
      width: 100%;
    }

    &:after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 0;
      height: 1px;
      background: #fff;
      transition: width 0.3s ease;
    }

    &:hover:after {
      width: 100%;
    }
  }

  @include media($min: 1360px) {
    min-width: 255px;
  }
}

.buisness-intro-section__inner {
  opacity: 0;
  transition: opacity 0.6s ease;
}

.buisness-intro-section__slider {
  .swiper-button {
    top: calc(58vh - 5%);
  }
  
  .swiper-button-prev {
    left: auto;
    right: calc(8% + 62px);
  }
  
  .swiper-button-next {
    right: calc(8% + 12px);
  }

  @include media($max: $screen-md) {
    .swiper-slide-next {
      opacity: 0.2;
    }

    .swiper-slide {
      width: 92% !important;
      transform: none !important;
    }

    .swiper-slide:last-of-type {
      width: 100% !important;
    }
  }

  @include media($min: $screen-md) {
    .swiper-button {
      top: auto;
      bottom: 30px;
    }

    .swiper-button-prev {
      left: auto;
      right: 80px;
    }
    
    .swiper-button-next {
      left: auto;
      right: 30px;
    }
  }

  @include media($min: 910px) {
    .swiper-button-prev {
      left: calc(50% + 330px);
      right: auto;
    }
    
    .swiper-button-next {
      left: calc(50% + 380px);
      right: auto;
    }
  }

  @media screen and (max-height: 500px) {
    .swiper-button {
      top: calc(300px - 6%);
    }
  }
}

.buisness-intro-section__slide {
  position: relative;
  background: $black;
}

.buisness-intro-section__slide-inner {
  display: flex;
  flex-direction: column;
  padding-bottom: 30px; 
  margin-top: 20px;

  @include media($min: $screen-md) {
    position: relative;
    min-height: 100vh;
    margin-top: 0;
  }
}

.buisness-intro-section__inner .heading {
  margin-bottom: 40px;

  @include media($max: $screen-md) {
    position: absolute;
    top: 0;
    left: 20px;
  }
}

.buisness-intro-section__slide-title {
  display: flex;
  justify-content: space-between;
  color: #fff;
  position: relative;
  padding-top: 20px;
  margin-bottom: 40px;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: #fff;
    transition: width 0.3s ease 0.3s;
  }

  @include media($max: $screen-md) {
    position: absolute;
    top: calc(58vh - 8%);

    left: 20px;

    &:before {
      content: none;
    }  
  }

  @media screen and (max-height: 500px) {
    position: absolute;
    top: calc(300px - 8%);
  }  
}

.buisness-intro-section__slide-img {
  display: flex;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 0.66;
  }

  @include media($max: $screen-md) {
    height: 58vh;
    min-height: 300px;
  }

  @include media($min: $screen-md) {
    position: absolute;
    top: 0;
    left: 58%;
    right: 0;
    bottom: 0;
  }
}

.buisness-intro-section__slide-icon {
  position: absolute;
  top: calc(58vh - 17%);
  left: 20px;
  width: 55px;
  height: 55px;

  svg {
    width: 100%;
    height: auto;
  }
  
  @include media($min: $screen-md) {
    top: 125px;
    left: auto;
    right: 30px;
    width: 90px;
    height: 90px;
  }

  @include media($min: $screen-lg) {
    top: 75px;
  }

  @include media($min: $screen-xl) {
    top: 175px;
    left: 30px;
  }

  @media screen and (max-height: 500px) {
    top: calc(300px - 17%);
  } 
}

.buisness-intro-section__slide-text {
  max-width: 400px;
  color: #fff;
  flex: 1;
  margin-bottom: 30px;

  @include media($max: $screen-md) {
    padding-right: 20px;
    margin-bottom: 40px;
  }
}

//on-scroll
.buisness-intro-section.on-scroll {
  overflow-y: auto;

  .buisness-intro-section__inner {
    opacity: 1;
  }
}

//Mobile
@include media($max: $screen-md) {
  .buisness-intro-section {
    overflow-y: auto;

    .buisness-intro-section__slide .heading {
      top: 50px;
    }

    .buisness-intro-section__inner {
      opacity: 1;
    }
  }
}
