.footer {
  color: #fff;

  @include media($min: $screen-md) {
    position: sticky;
    bottom: 0;
    z-index: 0;
  }
}

.footer__inner {
  display: flex;
  flex-direction: column;
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);
  min-height: 350px;
  padding-top: 20px;
  padding-bottom: 20px;
}

.footer__top {
  display: flex;
  flex-direction: column;
  flex: 1;
  
  @include media($min: $screen-md) {
    flex-direction: row;
  }
}

.footer__txt,
.footer__info {
  flex: 1;
}

.footer__txt {
  p {
    max-width: 310px;
    margin-top: 0;
  }
  
  @include media($max: $screen-md) {
    margin-bottom: 65px;
  }

  @include media($min: $screen-md) {
    padding-right: 5px;
  }
}

.footer__info  {
  display: flex;
  flex-direction: column;

  @include media($min: $screen-md) {
    flex-direction: row;
    padding-left: 5px;
  }
}

.footer__contacts-list {
  display: flex;
  flex-direction: column;

  @include media($max: $screen-md) {
    margin-bottom: 40px;
  }

  @include media($min: $screen-md) {
    max-width: 250px;
  }
}

.footer__address-list {
  display: flex;

  p {
    margin-top: 0;
    margin-bottom: 10px;
  }
  
  @include media($max: $screen-md) {
    margin-bottom: 32px;
  }

  @include media($min: $screen-md) {
    flex-direction: row;
    padding-left: 20px;
  }
}

.footer__address-item {
  flex: 1;

  &:not(:first-of-type) {
    margin-left: 20px;
  }

  @include media($min: $screen-md) {
    max-width: 200px;
  }
}

.footer__contacts-item {
  a {
    color: $accent-color;
    text-decoration: none;
  }

  &:not(:last-of-type) {
    margin-bottom: 10px;
  }

  @include media($max: $screen-md) {
    span {
      margin-right: 5px;
    }
  }

  @include media($min: $screen-md) {
    span {
      display: block;
    }
  }
}

.footer__list.footer__address-list {
  display: flex;
  flex-direction: column;
  
  @include media($min: $screen-md) {
    flex-direction: row;
  }
}

.footer__bottom {
  display: flex;

  @include media($max: $screen-md) {
    flex-direction: column;
  }
}

.footer__social-links {
  flex: 1;

  @include media($max: $screen-md) {
    margin-bottom: 22px;
  }

  @include media($min: $screen-md) {
    padding-right: 5px;
  }
}

.footer__copy {
  flex: 1;

  span {
    display: block;
  }

  @include media($min: $screen-md) {
    padding-left: 5px;
  }
}
