*, 
:after, 
:before, 
html {
  box-sizing: border-box;
}

html,
body {
  //height: 100%;
  min-height: 100%;
}

body {
  font-family: $base-font;
  color: $black;
  font-size: rem-calc(19px);
  line-height: rem-calc(30px);
  overflow: auto;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

