.works-section {
  position: relative;
  overflow: hidden;
  padding: 0;

  .heading-section {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $black;
    mix-blend-mode: darken;
    z-index: 2;
    transition: padding 1s ease, transform 0.6s ease, height 1s ease;
  }
}

.works-section__slide-inner {
  transform: translateY(100%);
  transition: transform 1s ease;
}

.works-section__slider {
  .swiper-button {
    top: auto;
    bottom: 20px;
  }

  .swiper-button-prev {
    left: auto;
    right: 70px;
  }
  
  .swiper-button-next {
    left: auto;
    right: 20px;
  }

  @include media($min: $screen-md) {
    .swiper-button {
      bottom: 30px;
    }

    .swiper-button-prev {
      right: 80px;
    }

    .swiper-button-next {
      right: 30px;
    }
  }

  @include media($min: 910px) {
    .swiper-button-prev {
      left: calc(50% + 330px);
      right: auto;
    }
    
    .swiper-button-next {
      left: calc(50% + 380px);
      right: auto;
    }
  }
}

.works-section__slide {
  background-color: $black;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.works-section__slide-inner {
  display: flex;
  flex-direction: column;  
  padding-bottom: 30px;
  position: relative;
  min-height: 100vh;
}

.works-section__slide-info-list {
  display: flex;
  position: relative;
  margin-top: 78px;
  padding-top: 20px;
  margin-bottom: 90px;
  flex: 1;
  
  @include media($max: $screen-md) {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  @include media($min: $screen-md) {
    margin-top: 40px;
    margin-bottom: 30px;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: #fff;
      transition: width 0.3s ease 0.3s;
    }
  }
}

.works-section__slide-info-item {
  text-align: center;
  
  &:not(:last-of-type) {
    margin-bottom: 23px;
  }

  @include media($min: $screen-md) {
    &:not(:last-of-type) {
      margin-right: 22px;
      margin-bottom: 0;
    }
  }
}

.works-section__slide-info-label {
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);
  color: #fff;
}

.works-section__slide-info-value {
  line-height: rem-calc(30px);
  color: #fff;
  margin-top: 5px;

  @include media($min: $screen-md) {
    line-height: rem-calc(40px);
    margin-top: 0;
  }
}

.works-section__slide-text {
  max-width: 420px;
  color: #fff;
  margin-bottom: 20px;

  @media screen and (max-width: $screen-md - 1px) {
    display: none;
  }
}

.works-section__slide-top {
  position: relative
}

.works-section__slide-info-platform {
  max-width: 97px;
  margin-top: 7px;

  svg {
    width: 100%;
    height: auto;
  }

  @include media($min: $screen-md) {
    position: absolute;
    top: calc(100% + 60px);    
    right: 0;
    max-width: 125px;
    margin-top: 0;
  }
}

//on-scroll
.works-section.on-scroll {
  overflow-y: auto;

  .heading-section {
    opacity: 1;
  }

  .works-section__slide-inner {
    transform: translateY(0);
  }
}

//Mobile
@include media($max: $screen-md) {
  .works-section {
    overflow-y: auto;

    .heading-section {
      opacity: 1;
    }

    .works-section__slide {
      padding-top: 50px;
    }

    .works-section__slide-inner {
      transform: translateY(0);
    }
  }
}
