.service-content-section {
  position: relative;
  padding-top: 90px;
  padding-bottom: 25px;
  z-index: 1;
  
  @include media($min: $screen-md) {
    padding-top: 160px;
    padding-bottom: 120px;
  }
}

.service-content__info {
  position: relative;
  padding-bottom: 10px; 

  @include media($min: $screen-md) {
    padding-bottom: 30px; 
  }
}

.service-content__info-sup-title {
  margin-bottom: 10px;
}

.service-content__info-title {
  max-width: 400px;
  margin-bottom: 20px;
  
  @include media($max: $screen-md) {
    border-bottom: 1px solid $black;
    padding-bottom: 37px;
    padding-right: 20px;
  }
}

.service-content__info-icon {
  position: absolute;
  top: 20px;
  left: calc(50% - 50vw + 30px);
  width: 93px;
  height: 93px;

  svg {
    width: 100%;
    height: auto;
  }

  svg rect,
  svg path {
    stroke: $black;
  }

  @include media($max: 1200px) {
    display: none;
  }
}

.service-content__info-text {
  max-width: 420px;
  margin-left: auto;
  margin-right: 0;
}


.service-content__txt-with-btn {
  text-align: center;
  color: $white;
  padding: 40px 0;
  margin-top: 30px;
  margin-bottom: 30px;

  p {
    margin-top: 0;
    margin-bottom: 10px;
  }
}


.service-content__info-list {
  @include media($min: $screen-md) {
    padding-top: 92px;
  }
}

.service-content__info-item {
  position: relative;
  padding: 38px 0;

  .text-bold {
    width: 100%;
    max-width: 400px;
    padding-right: 20px;
  }

  ul {
    flex: 1;
  }

  li {
    position: relative;
    padding-left: 40px;

    &:before {
      content: '';
      position: absolute;
      top: 15px;
      left: 0;
      width: 20px;
      height: 1px;
      background: $black;
    }
  }

  li + li {
    margin-top: 10px;
  }

  & + & {
    margin-top: 52px;
  }

  &.active:before {
    content: '';
    position: absolute;
    top: -30px;
    left: 0;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: $accent-color; 
  }

  &.active:after {
    content: '';
    position: absolute;
    top: -15px;
    left: 13px;
    width: 14px;
    height: 6px;
    border-bottom: 1px solid $white;
    border-left: 1px solid $white;
    transform: rotate(-45deg);
  }

  @include media($max: $screen-md) {
    .text-bold {
      margin-bottom: 22px;
    }

    li {
      margin-left: -40px;

      &:before {
        left: 10px;
      }
    }
  }

  

  @include media($min: $screen-md) {
    display: flex;

    & + & {
      margin-top: 44px;
      border-top: 1px solid $black;
    }

    &.active:before,
    &.active:after {
      content: none;
    }
  }

  @include media($min: 1200px) {
    &.active:before {
      content: '';
      top: 42px;
      left: calc(50% - 50vw + 30px);
      width: 90px;
      height: 90px;
    }
    
    &.active:after {
      content: '';
      top: 71px;
      left: calc(50% - 50vw + 58px);
      width: 33px;
      height: 18px;
    }
  }
}
