.social-list {
  display: flex;
}

.social-link {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  padding: 5px;
  border-radius: 50px;
  border: 1px solid rgba(255, 255, 255, 0.33);
  transition: background 0.3s ease;

  svg path {
    transition: fill 0.3s ease;
  }

  @include media($min: $screen-md) {
    &:hover {
      background: #fff;
  
      svg path {
        fill: $black;
      }
    }
  }
}
