.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  transition: background 0.2s ease, z-index 0s 0.65s; 
}

.header__inner {
  display: flex;
  align-items: center;
  padding: 10px;

  @include media($min: $screen-md) {
    padding: 19px 32px;
  }
}

.header__logo-and-btn {
  display: flex;
  align-items: center;
  transition: transform 0.65s ease;
  z-index: 999;
  opacity: 0;
  transform: translateX(-200px);
  transition: padding 0.65s ease, opacity 0.5s ease, transform 0.5s ease; 
}

.header__logo {
  display: flex;
  align-items: center;
  width: 50px;
  height: 50px;
  transition: padding 0.65s ease; 
  padding: 12px;
  background: $black;
  border-radius: 50%; 

  svg {
    width: 100%;
    height: auto;
  }

  @include media($max: $screen-md) {
    padding: 12px;
  }
}

.header__btn {
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 34px;
  padding: 6px;
  outline: none;
    
  &:before,
  &:after {
    content: '';
    display: block;
    height: 4px;
    border-radius: 2px;
    transition: all 0.65s ease;
    background: $black;
  }

  &:before {
    width: 34px;
    margin-bottom: 6px;
  }

  &:after {
    width: 24px;
  }

  @include media($min: $screen-md) {
    &:before,
    &:after {
      background: $black;
    } 
  }
}

.header__nav {
  flex: 1;
}

.header__nav-list {
  display: flex;
  align-items: center;
}

.header__nav-item:not(:last-of-type) {
  display: none;
  padding-left: 40px;
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 0.5s ease, transform 0.5s ease;
} 

.header__nav-item:last-of-type {
  flex: 1;
  text-align: right;
  opacity: 0;
  transform: translateX(100%);
  transition: opacity 0.5s ease, transform 0.5s ease;

  .header__nav-link {
    text-align: center;
    font-size: rem-calc(14px);
    line-height: 0;
    padding: 15px 30px;
    border-radius: 50px;
    background: $black;
    transition: background 0.3s ease, border-color 0.3s ease;

    &:before {
      content: none;
    }
  }

  @include media($max: $screen-md) {
    z-index: 999;
  }

  @include media($min: $screen-md) {
    .header__nav-link:hover {
      background: $accent-color;
      border-color: $accent-color;
    }
  }
}

.header__nav-link {
  position: relative;
  letter-spacing: 0.1em;
  color: #fff;

  &:before {
    content: '';
    position: absolute;
    bottom: -7px;
    left: 0;
    width: 0;
    height: 4px;
    border-radius: 2px;
    background: $accent-color;
    transition: width 0.3s ease;
  } 

  @include media($min: $screen-md) {
    &:hover:before {
      width: 100%;
    }
  }
}

.link-label-count {
  position: absolute;
  top: -9px;
  right: -20px;
  min-width: 20px;
  height: 20px;
  border-radius: 50%;
  font-size: rem-calc(9px);
  text-align: center;
  line-height: 20px;
  letter-spacing: 0;
  color: $font-color;
  background: $accent-color;
}


//header main section
.show-main {
  .header {
    display: block !important;
  }

  .header__inner {
    padding-top: 10px;
    padding-bottom: 10px;
  
    @include media($min: $screen-md) {
      padding-top: 20px;
      padding-bottom: 20px;
    }
  }
  
  .header__logo {
    padding: 0;
    background: transparent;
    
    @include media($max: $screen-md) {
      padding: 12px;
      background: $black;
    }

    @include media($min: $screen-md) {
      width: 40px;
    }
  }

  .header__btn {
    &:before,
    &:after {
      background: $white;
    }

    @include media($min: $screen-md) {
      display: none;
    }
  }

  .header__nav-item:not(:last-of-type) {
    display: block;

    @include media($max: $screen-md) {
      display: none;
    }
  }
  
  .header__nav-item:last-of-type .header__nav-link {
    font-size: rem-calc(19px);
    line-height: 30px;
    padding: 0;
    border-radius: 0;
    background: transparent;

    &:before {
      content: '';
      position: absolute;
      bottom: -7px;
      left: 0;
      width: 0;
      height: 4px;
      border-radius: 2px;
      background: $accent-color;
      transition: width 0.3s ease;
    } 

    @include media($min: $screen-md) {
      &:hover:before {
        width: 100%;
      }
    }
  }
}


//header menu
.header__menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-y: auto;
  visibility: hidden;
  background: $black;
  
  @include media($max: $screen-md) {
    top: 0;
    height: 100%;
    padding-top: 52px;  
    opacity: 0;
    transition: opacity 0.2s ease, visibility 0s 0.2s;
  }
  
  @include media($min: $screen-md) {
    background: transparent;
    padding-top: 0;
    transition: visibility 0s 0.65s;  
  }
}

.header__menu-inner {
  display: flex;
  align-items: stretch;
  min-height: 100%;
}

.header__menu-bg-animation {
  @include media($min: $screen-md) {
    position: fixed;
    top: 43px;
    left: 5px;
    width: 50px;
    height: 50px;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background: $black;
    transition: width 0.65s ease, height 0.65s ease; 
  }
}

.header__menu-info {
  display: flex;
  flex-direction: column;
  position: relative;
  padding: 20px;
  
  @include media($min: $screen-md) {
    flex-direction: row;
    flex: 1;
    flex-wrap: wrap;
    padding: 20px 20px 20px 18.2%;
  }
}

.header__menu-nav {
  margin-bottom: 47px;
  
  @include media($min: $screen-md) {
    flex: 3;
    margin-bottom: 0;
  }
}

.header__menu-nav-list {
  display: flex;
  flex-wrap: wrap;
}

.header__menu-nav-item {
  width: 100%;
}

.header__menu-nav-link {
  position: relative;
  font-family: $heading-font;
  font-weight: 500;
  font-size: rem-calc(44px);
  line-height: rem-calc(60px);
  letter-spacing: -0.02em;
  text-decoration: none;
  color: #fff;
  opacity: 0;
  transition: opacity 0.65s ease, line-height 0.35s ease 0.15s;
  
  
  &:before {
    content: '';
    position: absolute;
    top: 100%;
    left: 0;
    width: 0;
    height: 4px;
    border-radius: 2px;
    background: $accent-color;
    transition: width 0.3s ease;
  } 
  
  .link-label-count {
    top: -6px;
    right: -24px;
  }

  @include media($min: $screen-md) {
    font-size: rem-calc(64px);
    line-height: rem-calc(50px);

    &:hover:before {
      width: 100%;
    }

    .link-label-count {
      top: 8px;
    }
  }
}

.header__menu-contacts {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @include media($min: $screen-md) {
    flex: 2;
  }
}

.header__menu-contacts-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 190px;
  padding-top: 12px;
  
  @include media($max: $screen-md) {
    margin-bottom: 40px;
  }
}

.header__menu-contacts-item {
  width: 100%;
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);
  opacity: 0;
  transition: opacity 0.65s ease, transform 0.65s ease;

  span {
    display: block;
    color: #fff;
  }

  a {
    color: $accent-color;
    text-decoration: none;
  }

  &:not(:last-of-type) {
    margin-bottom: 10px;
  }

  @include media($min: $screen-md) {
    transform: translateY(-20px);
  }
}

.header__menu-form {
  display: none;
  position: relative;
  width: 100%;
  max-width: 360px;
  background: #fff;
  padding: 10px 30px 20px 20px;
  opacity: 0; 
  transition: opacity 0.35s ease;

  .contact-form-wrap {
    height: 100%;
    transform: translateY(-30px);
    opacity: 0; 
    transition: opacity 0.65s ease, transform 0.65s ease;
  }

  .form-btn {
    transform: translateY(50px);
    opacity: 0; 
    transition: opacity 0.65s ease, transform 0.65s ease;
  }

  .contact-form-heading {
    max-width: 180px;
  }

  .contact-form {
    display: flex;
    flex-direction: column;
    flex: 1;
  }

  .form-btn {
    display: flex;
    align-items: flex-end;
    flex: 1;
    margin-top: 20px;
  }

  @include media($min: $screen-md) {
    display: block;
  }
}

.header .social-list {
  opacity: 0;
  transition: opacity 0.65s ease, transform 0.65s ease; 

  @include media($min: $screen-md) {
    transform: translateY(20px);
  }
}


//Menu opened
.header.menu-opened {
  z-index: 999;
  transition: background 0.2s ease, z-index 0s 0s; 

  @include media($min: $screen-md) {
    background: transparent;
  }

  .header__menu {
    visibility: visible;
    transition: visibility 0s 0s;  

    @include media($max: $screen-md) {
      opacity: 1;
      transition: opacity 0.2s ease, visibility 0s 0s;
    }
  }

  .header__logo-and-btn {
    @include media($min: $screen-md) {
      transform: translateX(0);
    }
  }

  .header__logo {
    @include media($min: $screen-md) {
      background: transparent;
      padding: 6px 12px 6px 0;
    }
  }

  .header__btn {
    display: flex;
    
    &:before,
    &:after {
      background: #fff;
    }
    &:after {
      transform: translateX(10px);
    }
  }

  .header__menu-bg-animation {
    @include media($min: $screen-md) {
      width: 210vw;
      height: 210vw; 
    }
  }

  .header__menu-nav-link {
    opacity: 1;
    
    @include media($min: $screen-md) {
      line-height: rem-calc(80px);
    }
  }

  .header__menu-form {
    opacity: 1;
  }

  .header__menu-contacts-item,
  .social-list,
  .header__menu-form .contact-form-wrap,
  .header__menu-form .form-btn {
    opacity: 1;
    transform: translateY(0);
  }
}


body.on-load {
  .header__nav-item:not(:last-of-type) {
    opacity: 1;
    transform: translateY(0);
  }

  .header__nav-item:last-of-type {
    opacity: 1;
    transform: translateX(0);
  }
  
  .header__logo-and-btn {
    opacity: 1;
    transform: translateX(-30px);
    
    @include media($min: $screen-md) {
      transform: translateX(-51px);
    }
  }

  &.show-main .header__logo-and-btn {
    @include media($min: $screen-md) {
      transform: translateX(0);
    }
  }
}

.index-template {
  @include media($min: $screen-md) {
    .header {
      display: none;
    }
  }
}


//Color header
body.show-buisnes-intro,
body.show-works,
body.view-black-section {
  .header {
    .header__btn {
      &:before,
      &:after {
        background: $white;
      }
    }
  }
}
