
//Mixins
@mixin media($max: false, $min: false) {
  @if $max {
    @if $min {
      @media screen and (max-width: $max - 1) and (min-width: $min) { @content; }
    } @else {
      @media screen and (max-width: $max - 1) { @content; }
    }
  } @else {
    @media screen and (min-width: $min) { @content; }
  }
}

.showcase__section {
  @include media($min: $screen-md) {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    padding-bottom: 30px;
    transform: translateY(0);
    transition: transform .6s cubic-bezier(.825, 0, .5, 1);

    &.old-hat {
      transform: translateY(-100%);
    }

    &:last-of-type {
      padding-bottom: 0;
    }
  }
}

.bg-black {
  background-color: $black;
}

.bg-white {
  background-color: $white;
}

.heading-section {
  padding-top: 41vh;
  transition: padding 0.6s ease, height 0.6s ease; 
  
  .on-scroll & {
    opacity: 0;
    padding-top: 0;
    height: 0 !important;
    overflow: hidden;
  }

  @include media($max: $screen-md) {
    display: none !important;
  }

  @include media($min: $screen-md) {
    padding-top: 35vh;
  }
}

html.no-scroll,
html.no-scroll body {
  overflow: hidden;
}

.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 15px 30px;
  font-size: rem-calc(14px);
  line-height: rem-calc(19px);
  color: #fff;
  letter-spacing: 0.1em;
  border-radius: 50px;
  background: $accent-color;
  border: 1px solid $accent-color;
  cursor: pointer;
  outline: none;
  transition: background 0.3s ease, border-color 0.3s ease, color 0.3s ease;
  
  &.transparent {
    background: transparent;
  }

  &.bg-white {
    color: $accent-color;
    background: $white;
  }

  &.border-accent-color {
    border-color: $accent-color;
  }

  &.border-white {
    border-color: $white;
  }
  
  @include media($min: $screen-md) {
    &.transparent:hover,
    &.border-accent-color:hover{
      background: $accent-color;
    }

    &.bg-white:hover {
      background: $accent-color;
      color: $white;
    }
    
    &.border-white:hover {
      background: $white;
      color: $black;
    }
  }
}

.text-bold {
  font-family: $heading-font;
  font-weight: 900;
  font-size: rem-calc(28px);
  line-height: rem-calc(20px);
  letter-spacing: -0.02em;
  color: $black;

  @include media($min: $screen-md) {
    font-size: rem-calc(48px);
    line-height: rem-calc(40px);
  }
}

.heading {
  max-width: 640px;
  font-family: $heading-font;
  font-size: rem-calc(44px);
  line-height: rem-calc(40px);
  font-weight: 500;
  letter-spacing: -0.02em;
  color: #fff;
  margin: 0;
  
  &.black {
    color: $black;
  }

  .on-scroll & {
    padding-top: 92px;
  }

  //@include media($max: $screen-md) {
  //  .on-scroll & {
  //    padding-top: 20px;
  //  }
  //}

  @include media($min: $screen-md) {
    font-size: rem-calc(64px);
    line-height: rem-calc(60px);
  }

  @include media($min: $screen-lg)  {
    .on-scroll & {
      padding-top: 20px;
    }
  }
}


// Swiper
.swiper-button {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px !important;
  height: 50px !important;
  border-radius: 50%;
  background: #fff !important;
  outline: none;
  transition: background 0.2s ease;

  svg path {
    transition: stroke 0.2s ease;
  }

  @include media($min: $screen-md) {
    &:hover {
      background: $black !important;

      svg path {
        stroke: $white;
      }
    }
  }
}

.swiper-button-prev {
  transform: rotate(-90deg);
}

.swiper-button-next {
  transform: rotate(90deg);
}

