.team-section {
  padding-top: 0;
  padding-bottom: 15px;
  position: relative;
  padding-top: 60px;
  z-index: 1;
  
  .heading-section {
    display: none;
  }
  
  @include media($min: $screen-md) {
    padding-top: 160px;
    padding-bottom: 60px;
  }
}

.team-section__wrap {
  position: relative;
  z-index: 2;
}

.team-section__inner .heading {
  padding-bottom: 40px;
}

.team-section__txt {
  position: relative;
  padding-top: 20px;
  padding-right: 15px;
  margin-bottom: 40px;

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: $black;
    transition: width 0.6s ease 0.3s;
  }

  @include media($min: $screen-md) {
    margin-bottom: 80px;
  }
}

.team-section__txt-inner {
  max-width: 420px;
}

.team-section__list-wrap {
  @include media($max: $screen-md) {
    overflow-x: auto;
    padding: 0 20px;
    margin: 0 -20px;
  }
}

.team-section__list {
  @include media($max: $screen-md) {
    column-count: 6;
    width: 960px;
    font-size: 0;
    padding-bottom: 20px;
  }

  @include media($min: $screen-md) {
    display: flex;
    flex-wrap: wrap;
  }
}

.team-section__item {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 160px;
  height: 160px;
  border-radius: 50%;
  background-color: $white;
  overflow: hidden;

  &:not(.profile) {
    border: 1px solid $black;
  }

  
  @include media($min: $screen-md) {
    width: 215px;
    height: 215px;

    &.profile:hover .team-section__item-info  {
      opacity: 1;
    }
  }
}

.team-section__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  padding: 30px; 
  color: $black;
}

.team-section__item-avatar {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.team-section__item-info {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background: $accent-color;
  color: #fff;
  text-align: center;
  opacity: 0;
  padding: 30px;
  transition: opacity 0.3s ease;
}

.team-section__item-name {
  font-size: rem-calc(19px);
  line-height: rem-calc(20px);
  margin-bottom: 10px;
}

.team-section__item-desc,
.team-section__item-title {
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);
}

.index-template {
  .team-section {
    overflow: hidden;
    padding-top: 0;

    .heading-section {
      display: block;
    }
  }

  .team-section__inner {
    opacity: 0;
    transition: opacity 0.6s ease;
  }

  .team-section__txt:before {
    @include media($min: $screen-md) {
      width: 100%;
    }
  } 

  .team-section__txt-inner {
    opacity: 0;
    transform: translateY(-50px);
    transition: opacity 0.6s ease 0.3s, transform 0.6s ease 0.3s;
  }

  .team-section__item {
    opacity: 0;
    transition: opacity 0.6s ease 0.3s, transform 0.6s ease 0.3s;

    @include media($min: $screen-md) {
      &:nth-child(2n) {
        transform: translateX(-100%);
      }
    
      &:nth-child(2n + 1) {
        transform: translateX(-200%);
      }
    
      &:nth-child(4n) {
        transform: translateX(-300%);
      }

      &:nth-child(1),
      &:nth-child(5),
      &:nth-child(9) {
        transform: translateX(0);
      }
    }
  }
}

//on-scroll 
.index-template .on-scroll {
  overflow-y: auto;

  .team-section__inner {
    opacity: 1;
  }
  
  .team-section__txt:before {
    width: 100%;
  } 

  .team-section__txt-inner,
  .team-section__item {
    opacity: 1;
    transform: translateX(0);
  }
}

//Mobile {
@include media($max: $screen-md) {
  .index-template {
    overflow-y: auto;

    .team-section__inner {
      opacity: 1;
      padding-top: 50px;
    }

    .team-section__txt:before {
      width: 100%;
    }

    .team-section__txt-inner,
    .team-section__item {
      opacity: 1;
      transform: translateX(0);
    }
  }
}
