.scroll-down {
  display: flex;
  align-items: center;
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);
  margin-top: 25px;
  cursor: pointer;

  &.white {
    color: $white;
  }
}

.scroll-down-icon {
  position: relative;
  display: inline-block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background: $black;
  margin-right: 20px;

  &:before,
  &:after {
    content: '';
    position: absolute;
    top: 12px;
    left: 18px;
    width: 14px;
    height: 14px;
    border-bottom: 1px solid $white;
    border-left: 1px solid $white;
    transform: rotate(-45deg);
  }

  &:after {
    top: 18px;
  }

  .scroll-down.white & {
    background: $white;

    &:before,
    &:after {
      background-color: $black;
    }
  }
}
