.platforms-and-tools {
  padding-top: 90px;
  padding-bottom: 40px;
  overflow-y: auto;
  
  @include media($min: $screen-md) {
    padding-top: 120px;
    padding-bottom: 60px;
  }
}

.platforms-and-tools-list {
  display: flex;
  flex-wrap: wrap;
}

.platforms-and-tools-item {
  margin-bottom: 12px;

  &:not(:last-of-type) {
    margin-right: 30px;
  }

  @include media($min: $screen-md) {
    margin-bottom: 0;
  }
}

.platforms-and-tools-txt {
  max-width: 640px;
}

.platforms-and-tools-banner {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  padding: 0 20px;
  margin-top: 60px;
  
  svg {
    width: 100%; 
    max-width: 1300px;
  }

  .button {
    margin-top: 25px;
  }
  
  @include media($min: $screen-md) {
    padding: 0 30px;
    margin-top: 100px;

    .button {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      margin-top: 0;
    }
  }
}
