.showcase-pagination {
  display: none;
  left: 15px;
  position: absolute;
  top: 50%;
  z-index: 990;
  transform: translateY(-50%);

  .show-main & {
    display: none !important;
  }


  .dot {
    border-radius: 50%;
    box-shadow: inset 0 0 0 1px #000;
    cursor: pointer;
    height: 16px;
    opacity: .4;
    position: relative;
    width: 16px;
    box-sizing: border-box;
  }

  .dot.active {
    background: #000;
    box-shadow: none;
    opacity: 1;
  }

  @include media($max: $screen-md) {
    display: none !important;
  }
}

//Color pagination
body.show-buisnes-intro,
body.show-works {
  .showcase-pagination .dot {
    box-shadow: inset 0 0 0 1px #fff;
  }

  .showcase-pagination .dot.active {
    background: #fff;
  }
}
