.selectric-wrapper {
  max-width: 365px;
  padding-right: 30px;
  
  @include media($max: $screen-md) {
    padding: 0 30px 0 20px;
  }
}

.selectric {
  height: 80px;
  border: none;
  background: transparent;
  overflow: visible;

  .label {
    margin: 0;
    font-size: rem-calc(19px);
    line-height: rem-calc(30px);
    height: auto;
    padding-top: 32px;
  }
  
  .button {
    display: block;
    top: 25px; 
    right: -28px;
    padding: 10px;
    border-radius: 0;
    background: transparent;
    border: none;

    &:after {
      content: '';
      width: 8px;
      height: 8px;
      border-top: none;
      border-right: none;
      border-bottom: 1px solid $black;
      border-left: 1px solid $black;
      transform: rotate(-45deg);
    }
  }
}

.selectric-items {
  background: $white;
  border: none;
  box-shadow: 0px 5px 10px rgba(42, 40, 41, 0.1);

  li {
    font-size: rem-calc(19px);
    line-height: rem-calc(30px);
  }
}

.selectric-hide-select.selectric-is-native {
  width: auto;
}
