.clients-section {
  overflow: hidden;
  padding-top: 0;
}

.clients-section__inner {
  opacity: 0;
  transition: opacity 0.6s ease;
}

.clients-section__inner .heading {
  max-width: 100%;
  position: relative;
  padding-bottom: 40px;
  background: #fff;
  z-index: 2;
  overflow: hidden;

  &:before,
  &:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: $black;
    transition: width 0.6s ease 0.3s;
  }

  &:after {
    width: 100%;
    transform: translateX(100%);
    background: #fff;
  }

  &.running:after {
    animation: 0.6s linear running animationBorder;
  }

  @include media($min: $screen-md) {
    position: sticky;
    top: 0;
    padding-bottom: 42px;
    margin-bottom: calc(100vh - 230px);

    &:before,
    &:after {
      width: 0;
      bottom: 3px;
    }

    &:after {
      width: 100%;
    }
  }

  @include media($min: $screen-lg) {
    margin-bottom: calc(100vh - 152px);
  }
}

.clients-section__reviews {
  @include media($min: $screen-md) {
    display: flex;
    margin-top: calc(-100vh + 250px);
  }
  
  @include media($min: $screen-lg) {
    margin-top: calc(-100vh + 170px);
  }
}

.clients-section__reviews-right {
  flex: 1;
  min-width: 180px;
  order: 2;
  padding-top: 20px;
  padding-bottom: 10px;
  background: #fff;
  z-index: 1;

  @include media($min: $screen-md) {
    padding-bottom: 0;
    padding-top: 0;
    margin-top: 0;
    margin-bottom: 0;
    background: transparent;
  }
}

.clients-section__reviews-inner {
  flex-direction: column;
  align-items: flex-end;  
  position: sticky;
  top: 130px;
  
  @include media($min: $screen-md) {  
    top: 217px;
    display: flex;
    height: calc(100vh - 247px);
  }
  
  @include media($min: $screen-lg) {  
    height: calc(100vh - 172px);
    top: 142px;
  }
}

.clients-section__tabs {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  
  @include media($min: $screen-md) {
    order: 2;
    max-width: 180px;
    margin-bottom: 0;
  }
}

.clients-section__tab {
  display: inline-block;
  width: 80px;
  height: 80px;
  position: relative;
  border-radius: 50%;
  overflow: hidden;
  cursor: pointer;
  transition: background-color 0.25s ease, filter 0.25s ease;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &:not(.active) {
    background-color: $accent-color;

    img {
      filter: grayscale(100%) contrast(1);
      mix-blend-mode: multiply;
    }
  }

  &:hover {
    background-color: transparent;

    img {
      filter: none;
      mix-blend-mode: normal;
    }
  }

  @include media($min: $screen-md) {
    width: 90px;
    height: 90px;
  }
}

.clients-section__names-list {
  order: 1;
  flex: 1;
  opacity: 0;
  transform: translateY(100px);
  transition: opacity 0.3s ease 0.3s, transform 0.3s ease 0.3s;

  @include media($min: $screen-md) {
    margin-bottom: 30px;
  }
}

.clients-section__names-item {
  &:not(.active) {
    display: none;
  }

  &.active {
    animation: 0.3s linear running animationShow;
  }

  &.running {
    animation: 0.3s linear running animationHide;
  }

  @include media($min: $screen-md) {
    text-align: right;
  }
}

.clients-section__name {
  margin-bottom: 10px;

  @include media($min: $screen-md) {
    max-width: 180px;
  }
}

.clients-section__position {
  display: inline-block;
  max-width: 135px;
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);

  a {
    color: $black;
    text-decoration: underline;
  }
}

.clients-section__reviews-list {
  order: 1;
  opacity: 0;
  padding-bottom: 30px;
  transition: opacity 0.3s ease 0.3s, transform 0.3s ease 0.3s;

  p {
    margin: 0 0 10px;
  }

  @include media($min: $screen-md) {
    margin-top: 0;
  }
}

.clients-section__reviews-quotes {
  margin-bottom: 10px;
  text-align: right;

  @include media($min: $screen-md) {
    text-align: left;
  }
}

.clients-section__reviews-item {
  width: 100%;

  &:not(.active) {
    display: none;
  }

  &.active {
    animation: 0.3s linear running animationShow;
  }

  &.running {
    animation: 0.3s linear running animationHide;
  }

  @include media($min: $screen-md) {
    max-width: 400px;
  }
}

//on-scroll 
.clients-section.on-scroll {
  overflow-y: auto;

  .clients-section__inner {
    opacity: 1;
  }

  .clients-section__inner .heading:before {
    width: 100%;
  } 

  .clients-section__names-list,
  .clients-section__reviews-list {
    opacity: 1;
    transform: translateY(0);
  }
}

//Mobile
@include media($max: $screen-md) {
  .clients-section {
    overflow-y: auto;
    padding-top: 50px;

    .clients-section__inner {
      opacity: 1;
    }

    .clients-section__inner .heading:before {
      width: 100%;
    }

    .clients-section__names-list,
    .clients-section__reviews-list {
      opacity: 1;
      transform: translateY(0);
    }
  }
}

@keyframes animationShow {
  0% {
    opacity: 0;
    transform: translateY(25px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes animationHide {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(-25px);
    visibility: hidden;
  }
}

@keyframes animationBorder {
  0% {
    transform: translateX(100%)
  }
  100% {
    transform: translateX(-100%)
  }
}
