
.service-top-banner-section {
  @include media($max: $screen-md) {
    padding-left: 0;
    padding-right: 0;
  }
}

.service-top-banner-section__slider {
  .swiper-button {
    @include media($max: $screen-md) {
      position: fixed;

      &.not-fixed {
        position: absolute;
      }
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    top: auto;
    bottom: 30px;
  } 

  .swiper-button-prev {
    right: 70px;
    left: auto;
  }

  .swiper-button-next {
    right: 20px;
  }

  @include media($min: $screen-md) {
    .swiper-button-prev,
    .swiper-button-next {
      top: 60%;
    }
  }
   
  @include media($min: 1360px) {
    .swiper-button-prev,
    .swiper-button-next {
      top: 50%;
    }

    .swiper-button-prev {
      left: calc(50% + 325px);
      right: auto;
    }
    
    .swiper-button-next {
      left: calc(50% + 375px);
      right: auto;
    }
  }
}

.service-top-banner-section__slide {
  position: relative;
  padding-top: 91px;
  padding-bottom: 37px;
  color: $white; 
  background: $black;

  @include media($max: $screen-md) {
    padding-left: 20px;
    padding-right: 20px;
  }

  @include media($min: $screen-md) {
    padding-bottom: 30px;
  }
}

.service-top-banner-section__slide-top {
  display: flex;

  @include media($max: $screen-md) {
    flex-direction: column;
  }
}

.service-top-banner-section__tag {
  margin-bottom: 4px;
  
  @include media($min: $screen-md) {
    margin-bottom: 23px;
  }
}

.service-top-banner-section__heading {
  .heading { 
    max-width: 260px;
  }

  @include media($max: $screen-md) {
    margin-bottom: 140px;
  }
  
  @include media($min: $screen-md) {
    .heading {
      max-width: 400px;
    }
  }
}

.service-top-banner-section__img {
  @include media($max: $screen-md) {
    text-align: right;

    img {
      width: 100%;
      max-width: 400px;
    }
  }

  @include media($min: $screen-md) {
    margin-left: 60px;
  }

  @include media($min: 1360px) {
    margin-left: calc(50% - 590px);
  }
}

.service-top-banner-section__slide-bottom {
  @include media($max: $screen-md) {
    .container {
      padding: 0;
    }
  }

  @include media($min: $screen-md) {
    margin-top: -40px;
  }
}

.service-top-banner-section__review {
  @include media($min: $screen-md) {
    max-width: 420px;
  }
}

.service-top-banner-section__review-title {
  max-width: 230px;
  font-family: $heading-font;
  font-weight: 900;
  font-size: rem-calc(28px);
  line-height: rem-calc(30px);
  text-transform: uppercase;
  text-align: right;
  letter-spacing: -0.02rem;
  color: rgba(255, 255, 255, 0.33);
  margin: 10px 0 2px auto;
  
  @include media($min: $screen-md) {
    max-width: 100%;
    font-size: rem-calc(48px);
    line-height: rem-calc(40px);
    margin-bottom: 60px;
  }
}

.service-top-banner-section__info-list {
  margin-bottom: 47px;

  @include media($min: $screen-md) {
    margin-bottom: 40px;
  }

  @include media($min: 1360px) {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-55%);
    text-align: right;
  }
}

.service-top-banner-section__info-item {
  & + & {
    margin-top: 20px;
  }
}

.service-top-banner-section__info-label {
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);
}

.service-top-banner-section__info-value {
  color: $accent-color;
}

.service-top-banner-section__review-inner {
  display: flex;
  flex-direction: column;
}

.service-top-banner-section__review-client {
  @include media($max: $screen-md) {
    order: 2;
  }

  @include media($min: $screen-md) {
    display: flex;
    margin-bottom: 40px;
  }
}

.service-top-banner-section__review-avatar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  overflow: hidden;

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  @include media($max: $screen-md) {
    margin-bottom: 10px;
  }
}

.service-top-banner-section__review-details {
  @include media($max: $screen-md) {
    max-width: 160px;
  }

  @include media($min: $screen-md) {
    padding-left: 17px;
  }
}

.service-top-banner-section__review-position {
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);
  color: rgba(255, 255, 255, 0.33);
}

.service-top-banner-section__review-text {
  p {
    margin-top: 0;
    margin-bottom: 10px;
  }

  @include media($max: $screen-md) {
    order: 1;
    margin-bottom: 10px;
  }
}

.service-top-banner-section__slide-progress {
  position: absolute;
  top: auto;
  bottom: 90px;
  right: 20px;
  font-size: rem-calc(14px);
  line-height: rem-calc(20px);
  color: rgba(255, 255, 255, 0.33);
  z-index: 1;

  @include media($max: $screen-md) {
    position: fixed;

    &.not-fixed {
      position: absolute;
    }
  }

  @include media($min: $screen-md) {
    top: 56%;
  }

  @include media($min: 1360px) {
    top: 50%;
    left: 0;
    right: auto;
  }
}
